import { h, render, Component } from 'preact';
import axios from 'axios';

import Drawer from 'q-mdc/elements/material-drawer';
import DrawerContent from 'q-mdc/elements/material-drawer-content';
import DrawerHeader from 'q-mdc/elements/material-drawer-header';
import ListGroupHeader from 'q-mdc/elements/material-list-group-header';
import ListDivider from 'q-mdc/elements/material-list-divider';
import List from 'q-mdc/elements/material-list';
import ListItem from 'q-mdc/elements/material-list-item';
import Cookies from 'universal-cookie';

import get from 'lodash.get';

import { Ads, Categories, Contact, Feedback, Home, Languages, Location, MsgNotification, Msp, Quikr, Ratings, Share, ShortlistAds, View } from '../icons/drawerIcons';
import { PickupDrop, Bike, Car, Mobile, Electronics, Lifestyle, Homes, Services, Jobs, Education, Entertainment, Furniture, Pets, Community, Events, Matrimonial } from '../icons/categoryIcons';
import { Back, Account } from '../icons/headerIcons';

const categories = [
	{ name: 'Pick Up Drop Off',   icon: <PickupDrop class="mdc-temporary-drawer__category-icons" />, key: 'Pickup-dropoff', href: 'https://pudo.quikr.com' },
	{ name: 'Cars',   icon: <Car class="mdc-temporary-drawer__category-icons" />, key: 'cars-bikes', href: 'https://www.quikr.com/cars-bikes/gId-60' },
	{ name: 'Mobiles',  icon: <Mobile class="mdc-temporary-drawer__category-icons" />, key: 'mobiles-tablets', href: 'https://www.quikr.com/Escrow/mobiles-tablets/gId-269' },
	{ name: 'Electronics',  icon: <Electronics class="mdc-temporary-drawer__category-icons" />, key: 'electronics-appliances', href: 'https://www.quikr.com/Escrow/electronics-appliances/gId-247' },
	{ name: 'Lifestyle',  icon: <Lifestyle class="mdc-temporary-drawer__category-icons" />, key: 'home-lifestyle', href: 'https://www.quikr.com/Escrow/home-lifestyle/gId-40?filter=hl' },
	{ name: 'Bikes',  icon: <Bike class="mdc-temporary-drawer__category-icons" />, key: 'bikes', href: 'https://www.quikr.com/bikes/gId-72' },
	{ name: 'Homes', icon: <Homes class="mdc-temporary-drawer__category-icons" />, key: 'real-estate', href: 'https://www.quikr.com/real-estate/gId-20' },
	{ name: 'Services by Quikr Easy',  icon: <Services class="mdc-temporary-drawer__category-icons" />, key: 'services', href: 'https://www.quikr.com/services/gId-123' },
	{ name: 'Jobs',  icon: <Jobs class="mdc-temporary-drawer__category-icons" />, key: 'jobs', href: 'https://www.quikr.com/jobs' },
	{ name: 'Education',  icon: <Education class="mdc-temporary-drawer__category-icons" />, key: 'education-training', href: 'https://www.quikr.com/education-training' },
	{ name: 'Entertainment',  icon: <Entertainment class="mdc-temporary-drawer__category-icons" />, key: 'entertainment', href: 'http://www.quikr.com/entertainment/gId-179' },
	{ name: 'Furniture', icon: <Furniture class="mdc-temporary-drawer__category-icons" />, key: 'furniture-decor', href: 'https://www.quikr.com/Escrow/furniture-decor/gId-40?filter=fd' },
	{ name: 'Pets',  icon: <Pets class="mdc-temporary-drawer__category-icons" />, key: 'pets-pet-care', href: 'http://www.quikr.com/pets-pet-care/gId-246' },
	{ name: 'Community',  icon: <Community class="mdc-temporary-drawer__category-icons" />, key: 'community',href: 'https://www.quikr.com/community/gId-1' },
	{ name: 'Events',  icon: <Events class="mdc-temporary-drawer__category-icons" />, key: 'events', href: 'https://www.quikr.com/events/gId-281' },
	{ name: 'Matrimonial',  icon: <Matrimonial class="mdc-temporary-drawer__category-icons" />, key: 'matrimonial', href: 'https://www.quikr.com/matrimonial/gId-161'  }
];

const axiosInstance = axios.create({
	baseURL: 'https://www.quikr.com/',
	timeout: 1000,
	headers: {
		accept: 'application/json, text/javascript, */*; q=0.01',
		'accept-language': 'en-GB,en-US;q=0.9,en;q=0.8',
		'Content-Type': 'application/x-www-form-urlencoded'
	}
});

const cookies = new Cookies();

export default class Hamburger extends Component {
	constructor(props){
		super(props);
		this.state = {
			drawerData: [
				{ name: 'Home', id: 'homes', icon: <Home class="mdc-temporary-drawer__menu-icons" />,onClick: (e) => this.redirectToLink(e,'/') },
				{ name: 'Categories', id: 'categories', desc: 'Cars, Services, Mobiles, Electronics, Bazaar, Quikr Homes', arrow: true, icon: <Categories class="mdc-temporary-drawer__menu-icons"  />, onClick: this.openCategoriesMenu }
			],
			myActivities: [
				{ name: 'Ads', id: 'ads', icon: <Ads class="mdc-temporary-drawer__menu-icons" />, onClick: (e) => this.redirectToLink(e,'http://www.quikr.com/MyQuikr') }
				// { name: 'Messages & Notifications', id: 'msg-notification', icon: <MsgNotification class="mdc-temporary-drawer__menu-icons" /> }
				// { name: 'Shortlisted Ads', id: 'shortlist-ads', icon: <ShortlistAds class="mdc-temporary-drawer__menu-icons" /> },
				// { name: 'Recently Viewed Ads', id: 'view', icon: <View class="mdc-temporary-drawer__menu-icons" /> }
			],
			othersActivities: [
				// { name: 'Feedback', id: 'feedback', icon: <Feedback class="mdc-temporary-drawer__menu-icons" /> },
				// { name: 'Check Product MSP', id: 'msp', icon: <Msp class="mdc-temporary-drawer__menu-icons" /> },
				{ name: 'Language Settings', id: 'languages', icon: <Languages class="mdc-temporary-drawer__menu-icons" /> },
				// { name: 'Rate Us', id: 'ratings', icon: <Ratings class="mdc-temporary-drawer__menu-icons" /> },
				{ name: 'Bangalore', id: 'location', icon: <Location class="mdc-temporary-drawer__menu-icons" /> },
				{ name: 'About Quikr', id: 'quikr', icon: <Quikr class="mdc-temporary-drawer__menu-icons" />,  onClick: (e) => this.redirectToLink(e,'/html/about.php') },
				{ name: 'Contact Us', id: 'contact',icon: <Contact class="mdc-temporary-drawer__menu-icons" />, onClick: (e) => this.redirectToLink(e,'/html/contact.php') }
				// { name: 'Share App', id: 'share',icon: <Share class="mdc-temporary-drawer__menu-icons" /> }
			],
			pwaLinks: {},
			hostname: 'www.quikr.com',
			isCategoriesOpen: false
		};
	}

	componentDidMount() {
		this.getCategoryLinks();
	}

	redirectToLink = (e,link) => {
		e.preventDefault();
		e.stopPropagation();
		e.nativeEvent.stopImmediatePropagation();
		window.location = link;
	}
	getCategoryLinks = () => {

		/* Should be changed after city selection is added */
		let hostname = 'www.quikr.com';
		let cityName = cookies.get('new_prefer_city') || 'www';
		let cityId = cookies.get('prefer_city_id') || 1;
		let userId = false;
		let login = true;
		if ( window && window.location && window.location.hostname ) {
			hostname = window.location.hostname;
		}


		let params = new URLSearchParams();
		params.append('cityName', cityName);
		params.append('cityId', cityId);
		params.append('userId', userId);
		params.append('login', login);


		axiosInstance.post(`https://feservices.quikr.com/v2/header/get_links_pwa`,params).then((res) => {
			if (res.status === 200) {
				this.setState({
					pwaLinks: res.data.metaCats,
					hostname
				});
			}
			else {
				this.setState({
					pwaLinks: {},
					hostname
				});
			}
		}).catch(e => {
			this.setState({
				pwaLinks: {},
				hostname
			});
		});
	}

	openCategoriesMenu =(e) => {
		e.preventDefault();
		e.stopPropagation();
		e.nativeEvent.stopImmediatePropagation();
		this.setState({
			isCategoriesOpen: true
		});
	}

	closeCategoriesMenu = (e) => {
		e.preventDefault();
		e.stopPropagation();
		e.nativeEvent.stopImmediatePropagation();
		this.setState({
			isCategoriesOpen: false
		});
	}
	getNickName = (name) => {

		if (name){
			const splitName = name.split(' ');
			return splitName.length >= 2 ? splitName[0].charAt(0).toUpperCase() + splitName[1].charAt(0).toUpperCase() : splitName[0].charAt(0).toUpperCase();
		}
		return '';
	}

	render(props, state, context) {
		const { isLoggedIn, user } = props;
		const { isCategoriesOpen, pwaLinks, hostname } = state;
		return (
			<div>
				<Drawer
					ref={drawer => {
						this.drawer = drawer;
					}}
					open={props.drawerOpen}
					onOpen={(e) => {
						console.log('open');
					}}
					onClose={props.onClose}
				>
					<DrawerHeader>

						<a onClick={(e) => isLoggedIn ? this.redirectToLink(e,get(pwaLinks, 'URLS.myQuikrUrl', '')) : this.redirectToLink(e,`http://${hostname}/SignIn`)} >
							{isLoggedIn ? <figure><i>{user && this.getNickName(user.name)}</i></figure> : (<figure><i >
								<Account class="mdc-toolbar__icons" /></i></figure>) }
							<div class="details">
								{isLoggedIn ? (user && <p>{user.name}</p>) : <p>Login/Signup</p>}
								<span>My Account</span>
							</div>
							<i class="arrow-right" />
						</a>
					</DrawerHeader>

					<DrawerContent>
						<List>
							{this.state.drawerData.map((el) => (<ListItem onClick={el.onClick}>
								<i class="mdc-list-item__start-detail" aria-hidden="true" >
									{el.icon}
								</i>
								<p>{el.name}
									{ el.desc && <small class="mdc-temporary-drawer__sub-cates">{el.desc}</small>}
								</p>
								{ el.arrow && <i class="arrow-right" /> }
							</ListItem>))}

							<ListItem class="mdc-temporary-drawer__title">My Activities</ListItem>
							<ListDivider />
							{this.state.myActivities.map((activityData) => (<ListItem onClick={activityData.onClick}>
								<i class="mdc-list-item__start-detail" aria-hidden="true">
									{activityData.icon}
								</i>
								<p>{activityData.name}</p>
							</ListItem>))}

							<ListItem class="mdc-temporary-drawer__title">Others</ListItem>
							<ListDivider />
							{this.state.othersActivities.map((otherData) => (<ListItem onClick={otherData.onClick}>
								<i class="mdc-list-item__start-detail" aria-hidden="true">
									{otherData.icon}
								</i>
								<p>{otherData.name}</p>
							</ListItem>))}
						</List>
					</DrawerContent>
					
				</Drawer>
				<Drawer
					open={isCategoriesOpen}
					onClose={this.closeCategoriesMenu}
				>
					{isCategoriesOpen && <DrawerContent >
						<header class="mdc-list-item mdc-list-item__header" onClick={this.closeCategoriesMenu}>
							<i class="mdc-list-item__start-detail" aria-hidden="true"><Back class="mdc-temporary-drawer__menu-icons" /></i>
							<p>Categories</p>
						</header>
						<List class="mdc-list__category">
							<ListDivider />
							{categories.map((category,index) => (<ListItem key={index}>
								<a href={get(pwaLinks, `${category.key}.href`, category.href)} rel="noopener noreferrer">
									<i class="mdc-list-item__start-detail" aria-hidden="true">
										{category.icon}
									</i>
									<p>{category.name}</p>
								</a>
							</ListItem>))}
						</List>
					</DrawerContent>}
				</Drawer>
			</div>
		);
	}
}



